.home__cover_page {
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    padding-left: 30px;
    padding-right: 30px;
    background-image: linear-gradient( rgba(0, 0, 0, 0.4), 
    rgba(0, 0, 0, 0.9)), url('../statics/images/coverpage.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-color: #131211;
    color: #ffffff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.home__container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 70vh;
    /* background-color: #131211; */
}

.home__container p{
    text-align: center;
}

.home__main_title {
    font-size: 45px;
    font-weight: 1000;
}

.home__mini_title {
    font-size: 26px;
    font-weight: 900;

}

.home__animation_title {
    font-size: 30px;
    font-weight: 800;
}

@media all and (orientation:portrait) { 
    .home__container {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        width: 100%;
        height: 40vh;
    }

}

@media all and (orientation:portrait) and (min-width: 768px){ 
    .home__main_title {
        font-size: 80px;
    }
    
    .home__mini_title {
        font-size: 35px;
    }
    
    .home__animation_title {
        font-size: 60px;
    }
}

@media all and (orientation:landscape) and (min-width: 900px){ 

    .home__main_title {
        font-size: 80px;
    }
    
    .home__mini_title {
        font-size: 35px;
    }
    
    .home__animation_title {
        font-size: 60px;
    }

}





