#Contact {
    color: #2b2b33;
    display: flex;
    flex-direction: column;
}

.contact__banner {
    height: 50%;
    width: 100%;
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(to left, rgba(149, 47, 120, 0.6), rgba(77, 89, 153, 0.6)), url('../statics/images/banner.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: center center;
}

.contact__info {
    display: flex;
    flex-direction: column;
    height: 50%;
    width: 100%;
}

.contact__msg {
    font-size: 45px;
    font-weight: 800;
    color: #ffffff;
}

.contact__msg_2 {
    font-size: 20px;
    font-weight: 300;
    color: #ffffff;
    margin-bottom: 0px;   
}

.contact__info_box {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
    height: 50%;
}

.contact__info_text {
    color: #6b6e6e;
    display: flex;
    align-items: center;
    font-size: 18px;
}

.contact__info a {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #6b6e6e;
}

.contact__info a:hover {
    text-decoration: none;
    color: #F65070;
}

.contact__button_box {
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact__download_button {
    text-decoration: none;
    text-align: center;
    width: 200px;
    height: 50px;
    border-radius: 35px;
    border: none;
    background-color: #F65070;
    color: #ffffff;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
}

.contact__profile_pic_box {
    width: 100%;
    display: flex;
    justify-content: center;
}

.contact__profile_picture {
    border-radius: 50%;
    border: solid 4px #eeeeee;
    float: left;
    height: 150px;
    width: 150px;
}

.contact__download_button:hover {
    background-color: #fa6e89;
}

@media all and (orientation:landscape) and (min-width: 900px){
   
    .contact__msg {
        font-size: 45px;
    }
    .contact__msg_2 {
        font-size: 20px;
    }
}

@media all and (orientation:landscape) and (min-width: 480px) and (max-width: 899px) {
    .contact__banner {
        justify-content: flex-end;
        height: 65%;
    }
    .contact__info {
        height: 35%;
    }
    .contact__profile_picture {
        height: 100px;
        width: 100px;
    }
    .contact__msg {
        font-size: 35px;
    }
    .contact__msg_2 {
        font-size: 15px;
        margin-bottom: 15px;  
    }
    .contact__download_button {
        width: 180px;
        height: 40px;
        font-size: 15px;
    }
    .contact__info_text {
        font-size: 15px;
    }
}

@media all and (orientation:portrait) {
    .contact__box {
        height: 60%;
        width: 100%;
    }
    .contact__info_box {
        height: 60%;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
    }
    .contact__info_text {
        display: flex;
        align-items: center;
        font-size: 18px;
        margin-left: 30px;
        color: #2b2b33;
    }
    .contact__info a {
        color: #2b2b33;
    }
    .contact__button_box {
        height: 40%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}