#About {
  padding-top: 45px;
  display: flex;
  flex-direction: column;
}

.about_box {
  padding-left: 30px;
  padding-right: 30px;
  width: 100%;
  flex: 1 1 33%;
}

.about_title {
  font-size: 25px;
  font-weight: 500;
  color: #3a3e3e;
}

.about__extract {
  /* word-wrap: break-word; */
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.about__catching_Line {
  font-weight: normal;
}

.about__edu_title {
  font-size: 20px;
  font-weight: 400;
  color: #1B1B1E;
}

.about__edu_box_info {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
}

.about__edu_text {
  font-size: 10px;
  font-weight: 300;
}

.about__edu_deco_text {
  font-style: italic;
}

.about__skills {
  display: flex;
  color: #ffffff;
  background-color: #2b2b33;
  flex-direction: column;
  height: 33%;
}

.about__margin_title {
  display: flex;
  align-items: center;
  height: 40px;
}

.about__mid_section_title {
  font-size: 20px;
}

.about__mobile_skills_box {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.about__skills_box_info {
  display: flex;
  flex-direction: row;
  flex: 1 1;
}

.about__skill_icon_box {
  display: flex;
  align-items: center;
}

.about__icons {
  flex: 1 1;
}

.text {
  font-size: 15px;
}

@media all and (orientation:portrait) {
  .about__edu_box_info {
    flex-direction: column;
    justify-content: space-evenly;
    font-size: 15px;
  }
  .about__mobile_skills_box {
    flex-direction: row;
  }
  .about__skills_box_info {
    flex-direction: column;
  }
  .text {
    font-size: 15px;
  }
  .about__margin_title {
    height: 70px;
  }
}

@media all and (orientation:landscape) and (min-width: 900px) {
  .about_box {
    padding-left: 60px;
    padding-right: 60px;
  }
  .about_title {
    font-size: 45px;
  }
  .about__extract {
    width: 70%;
  }
  .about__edu_title {
    font-size: 30px;
  }
  .about__mid_section_title {
    font-size: 30px;
  }
  .text{
    font-size: 18px;
  }
  .about__margin_title {
    height: 100px;
  }
  .about__edu_box_info {
    font-size: 18px;
  }
  .about__edu_text {
    font-size: 12px;
  }  
}