#menu {
    z-index: 100;
    position: fixed;
    display: flex;
    width: 100%;
    height: 40px;
    padding-left: 60px;
    padding-right: 60px;
}

#options {
    padding-top: 1.5vh;
    position: absolute;
    right: 60px;
    visibility: visible;
}

.menu__mobile {
    visibility: hidden;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.menu__mobile_button {
    background-color: transparent;
    border: none;
    cursor: pointer;
}

.menu__dark {
    background-image: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3));
    /* background-color: #2b2b33; */
}

.menu__light {
    background-color: #eeeeee;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

.menu__mobile_options {
    width: 100%;
    z-index: 99;
    position: fixed;
    top: 40px;
    display: flex;
    flex-direction: column;
    padding-left: 30px;
    padding-right: 30px;
}

.menu__mobile_option {
    width: 100%;
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 500;
    text-decoration: none;
    height: 5.8vh;
}

@media (orientation: portrait)  {
    
    #menu {
        padding-left: 30px;
        padding-right: 30px;
        display: flex;
        justify-content: flex-end;
    }
    .menu__mobile {
        visibility: visible;
    }
    #options {
        visibility: hidden;
    }
    
} 


@media (orientation: landscape) and (max-width: 900px) {
    
    /* #menu {
        height: 10vh;
    } */
    #options {
        right: 30px;
    }
} 