.element {
  flex: 1 1 100%;
  padding: 0.5em;
}

.inner__element {

  /* padding-top: calc(100%/(16/9)); */
}

@media screen and (min-width: 480px) {
  .element {
    flex: 1 1 50%;
  }
}

@media screen and (min-width: 768px) {
  .element {
    flex: 1 1 33%;
  }
}

@media screen and (min-width: 980px) {
  .element {
    flex: 1 1 25%;
  }
}

.element__img{
  max-width: 100%;
  max-height: 100%;
  vertical-align: middle;
}